<template>
  <v-card class="ma-1" max-width="460" elevation="0" outlined>
    <v-card-title>{{ title }}</v-card-title>
    <v-card-text>
      <p>
        {{ description }}
      </p>
    </v-card-text>
    <v-card-actions class="pt-8">
      <v-btn absolute bottom right @click="goto" :disabled="!route">
        Open
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: 'SolutionCard',

  props: {
    title: {
      type: String,
      required: true,
    },

    description: {
      type: String,
      required: true,
    },

    route: {
      type: String,
      default: '',
    },
  },

  methods: {
    goto() {
      if (this.route.startsWith('http')) {
        window.open(this.route, '_blank', 'noreferrer')
      } else {
        this.$router.push({ name: this.route })
      }
    },
  },
}
</script>

